@use "./media.scss" as *;
@use "./fontSize.scss" as *;
@use "./color" as *;
@use "./function" as *;


main {
  min-height: calc(100vh - 20vw);

  h3 {
    @include h3;
  }
  
  #register {
    @extend %flex-center;
    flex-direction: column;
    min-height: calc(100vh - 20vw);

    @include mobile{
      min-height: 100vh
    }

    .title {
      font-weight: 600;
      color: $color-deep;
      margin-bottom: 10px;
      padding-top: 10vw;
    }

    .sign {
      #contact_form {
        width: 100%;
        height: 50vh;
        @extend %flex-center;
        flex-direction: column; 

        @include mac {
          height: 55vh;
        }

        label {
          font-size: 0.8rem;
          color: $color-deep;
          margin: 15px 0 5px;
        }
        input {
          width: 100%;
          font-family: "Noto Serif TC";
          font-size: 0.8rem;
          border: 0;
          border-bottom: 1px solid $color-deep;
          padding: 10px 10px; //修
          margin: 10px 0;
        }

        .sign1  {
          display: block;
          width: 100%;

          @include mobile {
            width: 80%;
          }
        }

        .sign2 {
          width: 100%;
          @extend %flex-between;

          @include mobile {
            width: 80%;
          }
          
          // 並列
          #username {
            margin-right: 5vw;
          }
        }
        .notice-box {
          padding: 1.5vw 0 1.5vw 0.5vw;

          p {
            color: $color-deep;
            font-size: 0.8rem;

            @include mobile {
              line-height: 120%;
              margin: 0 0 2vh 0;
            }
          }
        }
        .sign3 {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin: 2vw 0;
  
          #submit {
            color: $color-white;
            padding: 1vh 1vh;
            height: auto;
            width: 150px;
            cursor: pointer;
            border-radius: 16px;
            border: none;
            background-color: $color-pink;
  
            &:hover {
              box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
            }
          }
        }
      }

      
    }
  }
}
